import { Icon } from '@iconify/react'
import styles from './technology.module.scss'
import 'react-popper-tooltip/dist/styles.css'
import { usePopperTooltip } from 'react-popper-tooltip'
import classNames from 'classnames'
import SkillsLevel from '../SkillsLevel'

export interface TechnologyProps {
    readonly name: string
    readonly description?: string
    readonly level?: number
    readonly icon?: string
    readonly iconColor?: string
    readonly img?: string
}

function Technology({ name, description, level, icon, iconColor, img }: TechnologyProps) {
    const { getArrowProps, getTooltipProps, setTooltipRef, setTriggerRef, visible } =
        usePopperTooltip({ trigger: 'click' })
    const technologyLogo = (
        <>
            {icon && (
                <Icon
                    icon={icon}
                    color={iconColor}
                    className={styles.TechnologyIcon}
                    vAlign={'middle'}
                />
            )}
            {img && (
                <img
                    src={img}
                    alt={description ?? name}
                    style={{ height: '18px' }}
                    className={styles.TechnologyIcon}
                />
            )}
        </>
    )

    return (
        <>
            <span className={styles.Technology} data-tip={description} ref={setTriggerRef}>
                {technologyLogo}
                {(icon || img) && <> </>}
                <span itemProp={'keywords'}>{name}</span>
            </span>
            {visible && (
                <div
                    ref={setTooltipRef}
                    {...getTooltipProps({
                        className: classNames('tooltip-container', styles.TechnologyTooltip),
                    })}
                >
                    <div
                        {...getArrowProps({
                            className: classNames('tooltip-arrow', styles.TechnologyTooltipArrow),
                        })}
                    />
                    <div className={styles.TechnologyTooltipContent}>
                        {technologyLogo}
                        {description}
                        <div className={styles.TechnologyTooltipContentLevel}>
                            <SkillsLevel level={level} hover active />
                        </div>
                    </div>
                </div>
            )}
        </>
    )
}

export default Technology
