import Technology from '../../Technology'
import React from '../React'

interface GitlabProps {
    readonly level?: number
    readonly name?: string
}

function Gitlab({ level, name }: GitlabProps) {
    return (
        <Technology
            level={level}
            name={name ?? 'Gitlab'}
            description={'Gitlab'}
            icon={'logos:gitlab'}
        />
    )
}

export default Gitlab
