import styles from './skills-level.module.scss'
import classNames from 'classnames'

interface SkillsLevelProps {
    readonly level?: number
    readonly hover?: boolean
    readonly active?: boolean
}

function SkillsLevel({ level, hover, active }: SkillsLevelProps) {
    if (!level) {
        return <></>
    }

    return (
        <div className={styles.SkillsLevel}>
            {Array(5)
                .fill(0)
                .map((_, i) => (
                    <div
                        className={classNames(
                            styles.SkillsLevelMarker,
                            { [styles.SkillsLevelMarkerHover]: hover },
                            { [styles.SkillsLevelMarkerActive]: active },
                            {
                                [styles.SkillsLevelMarkerEmpty]: i >= level,
                            },
                        )}
                        key={i}
                    />
                ))}
        </div>
    )
}

export default SkillsLevel
