export function meanBy<T>(items?: T[], getter?: (item?: T) => number | undefined) {
    const values = items && getter ? items.map(getter) : []
    let sum = 0
    let cnt = 0
    values.forEach(value => {
        if (value !== undefined) {
            sum += value
            cnt += 1
        }
    })
    return cnt > 0 ? sum / cnt : 0
}
